.form {
    display: flex;
    flex-direction: column;
  }
  .formLabel{
    margin: 10px auto;
    color: blue;
  }
  .formName,
  .formNumber {
    display: flex;
    flex-direction: column;
    margin: 10px auto;
    align-items: center;
    
    text-align: left;
    padding: 5px 10px;
  }
  
  .formBtn {
    margin-top: 1rem;
    margin-bottom: 2rem;
    background-color: rgba(23, 26, 32, 0.8);
    border: none;
    border-radius: 6px;
    font-size: 15px;
    height: 36px;
    line-height: 20px;
    margin: 10px auto;
    min-width: 185px;
    padding: 0 16px 0 16px;
    color: white;
    cursor: pointer;
  }
  
  .formBtn:hover {
    background-color: rgba(23, 26, 32, 1);
    cursor: pointer;
  }