.contactList{
    display: flex;
    flex-direction: column;
    margin: 10px auto;
    align-items: center;
    padding: 5px 10px;
    list-style: none;
}
.contactListBtn {
    background-color: rgba(23,26,32,.8);
    border: none;
    border-radius: 6px;
    color: #fff;
    cursor: pointer;
    flex-grow: 1;
    font-size: 15px;
    height: 36px;
    line-height: 20px;
    margin: 8px;
    
    padding: 0 16px;
}